<template>
  <main class="portfolio">
    <div class="white-background-wrap">
      <div class="portfolio-intro" id="main" tabindex="0">
        <h2 class="section-header">portfolio<span v-if="$route.params.filterTag">{{' / ' + $route.params.filterTag}}</span></h2>
        <p>{{sectionText}}</p>
      </div>
    </div>
    <transition name="fade" mode="out-in">
      <router-view :pageType='"Portfolio"'></router-view>
    </transition>
  </main>
</template>

<script>
export default {
  name: 'portfolio',
  computed: {
    sectionText: function () {
      if (this.$route && this.$route.params) {
        switch (this.$route.params.filterTag) {
          case 'charity':
            return 'Whether it was eating hot wings, singing, or just being herself, Lowco has time and again found ways to rally her community to support great causes throughout the years. The Looneybin has raised over $50,000 for charity!'
          case 'industry':
            return 'Lowco loves her career and the people who make it possible. She has appeared on Origin PC live and runs her own website, StreamerSquare, a resource for streamers and just about anyone else in the Industry.'
          case 'promotions':
            return 'Lowco accepts opportunities to showcase and create interest in games, in fact they are an integral part of her success as a streamer. A synopsis of many such opportunities can be found here.'
          default:
            return 'All of Lowco\'s major promotional streams have been chronicled here as well as write-ups of the many ways she has found to put others needs ahead of her own through charitable work.'
        }
      } else {
        return 'All of Lowco\'s major promotional streams have been chronicled here as well as write-ups of the many ways she has found to put others needs ahead of her own through charitable work.'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
.portfolio-intro{
  padding: $home-section-padding;
  max-width: 600px;
  height: 162px;
  margin: 4em auto 0 auto;
}
</style>



